import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import moment from "moment";
import NumberFormat from "../../../App/NumberFormat";

import notification from "../../../../components/notification";
import {
  Col,
  Collapse,
  DatePicker,
  Divider,
  Empty,
  // Input,
  Row,
  Select,
  Spin,
  Tooltip
} from "antd";

// import Popconfirms from "../../../../components/feedback/popconfirm";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/report/accPayDueReport/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper, { Box, customCollapseStyle } from "../../../App/app.style";
import PrintWrapper from "../../../App/PrintWrapper";
import ReactToPrint from "react-to-print";
import ButtonPrint from "../../../App/ButtonPrint";
import ExcelWrapper from "../../../App/ExcelWrapper";
import PDFWrapper from "../../../App/PDFWrapper";
import getLanguage from "../../../App/MyLanguange";
import { getFileName } from "../../../../helpers/helper";
import { siteConfig } from "../../../../settings";

const Panel = Collapse.Panel;
const Option = Select.Option;
// const Search = Input.Search;
const { RangePicker } = DatePicker;

const redirectTo = "/dashboard/report-menus";

class AccPayDueReport extends Component {
  state = {
    filter: {
      id_location: null,
      id_supplier: null,
      start_date: moment()
        .startOf("month")
        .format("YYYY-MM-DD HH:mm:ss"),
      end_date: moment().format("YYYY-MM-DD HH:mm:ss")
    },
    search: null
  };

  // Get All data diawal
  componentDidMount() {
    const { getAccPayDueReports } = this.props;
    getAccPayDueReports();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.accPayDueReport.error) {
      notification("error", this.props.accPayDueReport.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.accPayDueReport.success) {
      notification("success", this.props.accPayDueReport.success);
      this.props.clearSuccess();
    }
  }

  // Handle search
  handleSearch = value => {
    const { getAccPayDueReports } = this.props;
    let filter = this.filterValue();

    getAccPayDueReports(1, value, filter, this.state.filter);
  };

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;
    this.setState({ search: value });
  };

  // Handle Reset search
  handleResetSearch = () => {
    const { getAccPayDueReports } = this.props;
    this.setState({
      filter: {
        id_location: null,
        id_supplier: null,
        start_date: moment()
          .startOf("month")
          .format("YYYY-MM-DD HH:mm:ss"),
        end_date: moment().format("YYYY-MM-DD HH:mm:ss")
      },
      search: null
    });
    getAccPayDueReports();
  };

  //   Handle filter change
  handleFilterChange = (name, value) => {
    if (name === "date" && value[0] && value[1]) {
      const start = value[0].format("YYYY-MM-DD HH:mm:ss");
      const end = value[1].format("YYYY-MM-DD HH:mm:ss");
      // console.log(start, end);
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: start,
          end_date: end
        }
      });
    } else if (name === "date" && !value[0] && !value[1]) {
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: null,
          end_date: null
        }
      });
    } else {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value
        }
      });
    }
    if (name === "id_location") {
      this.setState({
        filter: {
          ...this.state.filter,
          id_location: value,
          id_supplier: null
        }
      });
    }
  };

  filterValue = () => {
    const { filter } = this.state;
    let string = "";

    if (filter.id_location) {
      string += "id_location=" + filter.id_location;
    }
    if (filter.id_supplier) {
      string
        ? (string += "&id_supplier=" + filter.id_supplier)
        : (string += "id_supplier=" + filter.id_supplier);
    }
    if (filter.start_date && filter.end_date) {
      string
        ? (string +=
            "&start_date=" + filter.start_date + "&end_date=" + filter.end_date)
        : (string +=
            "start_date=" + filter.start_date + "&end_date=" + filter.end_date);
    }

    return string;
  };

  // Handle filter
  handleFilter = () => {
    const { getAccPayDueReports } = this.props;
    //const { current_page } = this.props.accPayDueReport;
    let filter = this.filterValue();

    getAccPayDueReports(1, null, filter, this.state.filter);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getAccPayDueReports } = this.props;
    this.setState({
      filter: {
        id_location: null,
        id_supplier: null,
        start_date: moment()
          .startOf("month")
          .format("YYYY-MM-DD HH:mm:ss"),
        end_date: moment().format("YYYY-MM-DD HH:mm:ss")
      },
      search: null
    });
    getAccPayDueReports();
  };

  render() {
    const columns = [
      {
        title: getLanguage({ id: "invoiceNo" }),
        dataIndex: "purchase_invoice_no",
        rowKey: "purchase_invoice_no",
        width: "12%",
        render: (text, row) => {
          if (row.key === "Total") {
            return {
              children: (
                <strong style={{ marginLeft: "50%" }}>{row.key}</strong>
              ),
              props: {
                colSpan: 8
              }
            };
          }

          return text;
        }
      },
      {
        title: getLanguage({ id: "supplierName" }),
        dataIndex: "supplier_name",
        rowKey: "supplier_name",
        width: "10%",
        render: (text, row) => {
          if (row.key === "Total") {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          return text;
        }
      },
      {
        title: getLanguage({ id: "transactionDate" }),
        dataIndex: "transaction_date",
        date: true,
        rowKey: "transaction_date",
        width: "10%",
        render: (text, row) => {
          if (row.key === "Total") {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          return moment(text).format("DD MMMM YYYY");
        }
      },
      {
        title: getLanguage({ id: "dueDate" }),
        date: true,
        dataIndex: "due_date",
        rowKey: "due_date",
        width: "10%",
        render: (text, row) => {
          if (row.key === "Total") {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          return moment(text).format("DD MMMM YYYY");
        }
      },
      {
        title: getLanguage({ id: "dueDays" }),
        dataIndex: "due_days",
        rowKey: "due_days",
        width: "8%",
        render: (text, row) => {
          if (row.key === "Total") {
            return {
              props: {
                colSpan: 0
              }
            };
          }
          return text;
        }
      },
      {
        title: getLanguage({ id: "sidebar.paymentCategory" }),
        dataIndex: "payment_category",
        rowKey: "payment_category",
        width: "8%",
        render: (text, row) => {
          if (row.key === "Total") {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          return text;
        }
      },
      {
        title: getLanguage({ id: "sidebar.paymentTerm" }),
        dataIndex: "payment_term",
        rowKey: "payment_term",
        width: "9%",
        render: (text, row) => {
          if (row.key === "Total") {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          return text;
        }
      },
      {
        title: getLanguage({ id: "sidebar.currency" }),
        dataIndex: "currency",
        rowKey: "currency",
        width: "9%",
        render: (text, row) => {
          if (row.key === "Total") {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          return text;
        }
      },
      {
        title: getLanguage({ id: "totalAmount" }),
        dataIndex: "total_amount",
        rowKey: "total_amount",
        width: "13%",
        align: "right",
        parse: true,
        render: (text, row) => {
          if (row.key === "Total") {
            return (
              <strong>
                <NumberFormat value={parseFloat(text)} />
              </strong>
            );
          }

          return <NumberFormat value={parseFloat(text)} />;
        }
      },
      {
        title: getLanguage({ id: "paidAmount" }),
        dataIndex: "paid_amount",
        rowKey: "paid_amount",
        width: "13%",
        align: "right",
        parse: true,
        render: (text, row) => {
          if (row.key === "Total") {
            return (
              <strong>
                <NumberFormat value={parseFloat(text)} />
              </strong>
            );
          }

          return <NumberFormat value={parseFloat(text)} />;
        }
      }
    ];

    const excelColumns = [
      {
        title: getLanguage({ id: "invoiceNo" }),
        dataIndex: "purchase_invoice_no",
        rowKey: "purchase_invoice_no",
        width: "10%"
      },
      {
        title: getLanguage({ id: "supplierName" }),
        dataIndex: "supplier_name",
        rowKey: "supplier_name",
        width: "20%"
      },
      {
        title: getLanguage({ id: "transactionDate" }),
        date: true,
        dataIndex: "transaction_date",
        rowKey: "transaction_date",
        width: "20%",
        align: "right",
        render: (text, row) => {
          if (row.key === "Total") {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          return moment(text).format("DD MMMM YYYY");
        }
      },
      {
        title: getLanguage({ id: "dueDate" }),
        date: true,
        dataIndex: "due_date",
        rowKey: "due_date",
        width: "20%",
        align: "right",
        render: (text, row) => {
          if (row.key === "Total") {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          return moment(text).format("DD MMMM YYYY");
        }
      },
      {
        title: getLanguage({ id: "dueDays" }),
        dataIndex: "due_days",
        rowKey: "due_days",
        width: "20%",
        align: "right"
      },
      {
        title: getLanguage({ id: "sidebar.paymentCategory" }),
        dataIndex: "payment_category",
        rowKey: "payment_category",
        width: "20%",
        align: "right"
      },
      {
        title: getLanguage({ id: "sidebar.paymentTerm" }),
        dataIndex: "payment_term",
        rowKey: "payment_term",
        width: "20%",
        align: "right"
      },
      {
        title: getLanguage({ id: "sidebar.currency" }),
        dataIndex: "currency",
        rowKey: "currency",
        width: "20%",
        align: "right"
      },
      {
        title: getLanguage({ id: "totalAmount" }),
        dataIndex: "total_amount",
        rowKey: "total_amount",
        width: "20%",
        align: "right",
        parse: true,
        render: (text, row) => {
          if (row.key === "Total") {
            return (
              <strong>
                <NumberFormat value={parseFloat(text)} />
              </strong>
            );
          }

          return <NumberFormat value={parseFloat(text)} />;
        }
      },
      {
        title: getLanguage({ id: "paidAmount" }),
        dataIndex: "paid_amount",
        rowKey: "paid_amount",
        width: "20%",
        align: "right",
        parse: true,
        render: (text, row) => {
          if (row.key === "Total") {
            return (
              <strong>
                <NumberFormat value={parseFloat(text)} />
              </strong>
            );
          }

          return <NumberFormat value={parseFloat(text)} />;
        }
      }
    ];

    // Data setup
    const {
      acc_pay_due_reports,
      locations,
      suppliers,
      loading,
      filter,
      filter_select
    } = this.props.accPayDueReport;
    //console.log(this.props.accPayDueReport.base_currency); //console.log(this.state);
    const dataSource = [];

    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    // let total_amounts = 0;
    // let total_paid_amounts = 0;
    acc_pay_due_reports.map((data, key) => {
      function daysRemaining() {
        var ddate = moment(data.due_date, "YYYY-MM-DD");
        var todaysdate = moment();
        return ddate.diff(todaysdate, "days");
      }
      data.due_days = daysRemaining();

      dataSource.push({
        ...data,
        key
      });
    });

    const fileName = getLanguage({ id: "accPayDueReports.pageHeader" })

    // dataSource.push({
    //   key: "Total",
    //   total_amount: total_amounts,
    //   paid_amount: total_paid_amounts
    // });

    //console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="accPayDueReports.pageHeader" />}
            marginBottom={true}
          >
            <div>
              <PrintWrapper
                componentRef={ref => (this.componentRef = ref)}
                title={<IntlMessages id="accPayDueReports.pageHeader" />}
                columns={excelColumns}
                data={dataSource}
                filter={filter_select}
                locations={locations}
              />

              <ReactToPrint
                trigger={() => <ButtonPrint loading={loading} />}
                content={() => this.componentRef}
                onBeforePrint={() => (document.title = getFileName(fileName, null, false))}
                onAfterPrint={() => (document.title = siteConfig.documentTitle)}
                pageStyle={
                  "@page { size: landscape; }" +
                  "body {" +
                  "-webkit-print-color-adjust: exact;" +
                  "} "
                }
              />

              <PDFWrapper
                columns={columns}
                data={dataSource}
                fileName={fileName}
                filter={filter_select}
                landscape={true}
                loading={loading}
                locations={locations}
                margin=".8cm"
                scale={0.5}
                title="accPayDueReports.pageHeader"
              />

              <ExcelWrapper
                title="accPayDueReports.pageHeader"
                fileName={fileName}
                loading={loading}
                columns={excelColumns}
                data={dataSource}
                filter={filter_select}
                locations={locations}
              />

              <Button
                onClick={() => this.props.history.replace(redirectTo)}
                style={{ marginLeft: "5px" }}
              >
                <IntlMessages id="button.back" />
              </Button>
            </div>
          </PageHeader>

          {/* <div className="isoInvoiceTableBtn">
            <Search
              placeholder="Location Code, Location Name"
              onSearch={this.handleSearch}
              style={{ marginRight: "1%" }}
              value={this.state.search}
              onChange={this.handleSearchValue}
            />
            <div
              style={{
                width: "20%",
                marginTop: "auto",
                marginRight: "1%"
              }}
            >
              <Button
                type="primary"
                onClick={this.handleSearch.bind(this, this.state.search)}
              >
                <IntlMessages id="button.searchFilter" />
              </Button>
            </div>

            <div
              style={{
                width: "30%",
                marginTop: "auto",
                marginRight: "27%"
                // marginLeft: "-2%"
              }}
            >
              <Button onClick={this.handleResetSearch}>
                <IntlMessages id="filter.reset" />
              </Button>
            </div>
          </div> */}

          <div className="isoFilter">
            <Collapse style={customCollapseStyle} defaultActiveKey={["1"]}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="salesOrders.filter.date" />
                    </label>
                    <br />
                    <RangePicker
                      format="DD MMMM YYYY HH:mm:ss"
                      showTime
                      allowClear={false}
                      style={{ width: "95%" }}
                      value={
                        this.state.filter.start_date &&
                        this.state.filter.end_date
                          ? [
                              moment(this.state.filter.start_date),
                              moment(this.state.filter.end_date)
                            ]
                          : []
                      }
                      onChange={this.handleFilterChange.bind(this, "date")}
                    />
                  </Col>
                  <Col span={12}>
                    <Row justify="start" gutter={1} type="flex">
                      <Col span={24}>
                        <label>
                          <IntlMessages id="sidebar.location" />
                        </label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "id_location"
                          )}
                          value={
                            this.state.filter.id_location
                              ? this.state.filter.id_location
                              : undefined
                          }
                          style={{ width: "100%" }}
                          placeholder={<IntlMessages id="select.location" />}
                          optionFilterProp="children"
                          filterOption={true}
                          allowClear={true}
                          //showArrow={false}
                        >
                          {locations && locations.length > 0
                            ? locations.map((data, key) => (
                                <Option key={key} value={data.id_location}>
                                  {data.location_code +
                                    " (" +
                                    data.location_name +
                                    ")"}
                                </Option>
                              ))
                            : null}
                        </Select>
                      </Col>
                    </Row>
                  </Col>
                </Row>
                <Row
                  justify="space-between"
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <Row justify="start" gutter={1} type="flex">
                      <Col span={16}>
                        <label>
                          <IntlMessages id="sidebar.supplier" />
                        </label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "id_supplier"
                          )}
                          value={
                            this.state.filter.id_supplier
                              ? this.state.filter.id_supplier
                              : undefined
                          }
                          style={{ width: "95%" }}
                          placeholder={
                            this.state.filter.id_location ? (
                              <IntlMessages id="select.supplier" />
                            ) : (
                              <IntlMessages id="noLocation" />
                            )
                          }
                          optionFilterProp="children"
                          filterOption={true}
                          allowClear={true}
                          notFoundContent={
                            loading ? (
                              <Spin size="small" />
                            ) : this.state.filter.id_location ? (
                              <Empty
                                description={<IntlMessages id="noData" />}
                              />
                            ) : (
                              <Empty
                                description={
                                  <IntlMessages id="purchaseOrders.item.noLocation" />
                                }
                              />
                            )
                          }
                          //showArrow={false}
                        >
                          {suppliers && suppliers.length > 0
                            ? suppliers
                                .filter(
                                  data =>
                                    data.id_location ===
                                    this.state.filter.id_location
                                )
                                .map((data, key) => (
                                  <Option key={key} value={data.id_supplier}>
                                    {data.supplier_code +
                                      " (" +
                                      data.supplier_name +
                                      ")"}
                                  </Option>
                                ))
                            : null}
                        </Select>
                      </Col>
                      <Col
                        span={8}
                        style={{
                          marginTop: "auto"
                        }}
                      >
                        <Button
                          size="small"
                          type="primary"
                          style={{ marginRight: "10px" }}
                          onClick={this.handleFilter}
                        >
                          <IntlMessages id="filter.only" />
                        </Button>
                        <Button onClick={this.handleResetFilter} size="small">
                          <IntlMessages id="filter.reset" />
                        </Button>
                      </Col>
                    </Row>
                  </Col>
                </Row>
              </Panel>
            </Collapse>
          </div>

          <CardWrapper>
            {!filter ? (
              <Empty description={<IntlMessages id="clickFilter" />} />
            ) : acc_pay_due_reports.length === 0 &&
              !this.props.accPayDueReport.loading ? (
              <Empty
                description={
                  <IntlMessages id="accPayDueReports.table.noData" />
                }
              />
            ) : (
              <div className="isoInvoiceTable">
                <Divider orientation="left" style={{ fontSize: 14 }}>
                  <IntlMessages id="details" />
                </Divider>
                <Scrollbars style={{ width: "100%" }}>
                  <TableWrapper
                    dataSource={dataSource}
                    columns={columns}
                    loading={loading}
                    className="invoiceListTable"
                    bordered={true}
                    // Set pagination, jika ada page_size mengunakan pagination API
                    // jika tidak ada page_size menggunakan pagination bawaan
                    pagination={false}
                  />
                </Scrollbars>
              </div>
            )}
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    accPayDueReport: state.accPayDueReport,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, actions))(AccPayDueReport);
